import { Navigate } from "react-router-dom";
import { getCurrentUser } from "../services/r-agent";

export function PrivateRoute({ redirectTo, userRole, children }) {
  const currentUser = getCurrentUser();

  if (!currentUser || currentUser.role !== userRole) {
    return <Navigate to={redirectTo} replace={true} />;
  }

  return children(currentUser);
}

export function RAgentPrivateRoute({ children }) {
  return (
    <PrivateRoute redirectTo="/r-agent/login" userRole="r-agent">
      {children}
    </PrivateRoute>
  );
}
