import { useNavigate } from "react-router-dom";
import { logOutCurrentUser } from "../services/r-agent";

function RAgentLogout({disabled}) {
  const navigate = useNavigate();

  const onLogout = () => {
    logOutCurrentUser();
    navigate("/r-agent/login", { replace: true });
  };

  return (
    <div>
      <button disabled={disabled} onClick={onLogout}>LOG OUT</button>
    </div>
  );
}

export default RAgentLogout;
