import { Link } from "react-router-dom";
import { RAgentPrivateRoute } from "../../components/PrivateRoute";
import RAgentLogout from "../../components/RAgentLogout";

function RAgentHome({ currentUser }) {
  return (
    <main>
      <h1>Hello, {currentUser.name.split(" ")[0]}</h1>
      <RAgentLogout />
      <nav>
        <Link to="/r-agent/register-student">Register Student</Link>
      </nav>
    </main>
  );
}

function Page() {
  return (
    <RAgentPrivateRoute>
      {(currentUser) => <RAgentHome currentUser={currentUser} />}
    </RAgentPrivateRoute>
  );
}

export default Page;
