import { useEffect, useState } from "react";
import { useForm } from "react-hook-form";
import { RAgentPrivateRoute } from "../../components/PrivateRoute";
import RAgentLogout from "../../components/RAgentLogout";
import { createStudentAccount, getSchools } from "../../services/r-agent";

let formMessageTimeout = null;

function RAgentRegisterStudent() {
  const [schools, setSchools] = useState(null);
  const [schoolsLoading, setSchoolsLoading] = useState(false);
  const [schoolsError, setSchoolsError] = useState(null);

  useEffect(() => {
    const run = async () => {
      try {
        setSchoolsLoading(true);
        const schools = await getSchools();
        if (schools.length === 0) {
          setSchoolsError("No schools in the database");
        } else {
          setSchools(schools);
        }
      } catch (error) {
        setSchoolsError("something went wrong while getting school list");
      } finally {
        setSchoolsLoading(false);
      }
    };
    run();
  }, []);

  const {
    register,
    handleSubmit,
    formState: { errors, isSubmitting },
    reset,
  } = useForm();
  const [formMessage, setFormMessage] = useState(null);

  const onSubmit = async (data) => {
    setFormMessage(null);
    clearTimeout(formMessageTimeout);
    try {
      await createStudentAccount(data);
      reset();
      setFormMessage("SUCCESS: student account created");
      formMessageTimeout = setTimeout(() => {
        setFormMessage(null);
      }, 4000);
    } catch (error) {
      setFormMessage(`ERROR: ${error.message}`);
      formMessageTimeout = setTimeout(() => {
        setFormMessage(null);
      }, 4000);
    }
  };
  console.table(errors);
  return (
    <main>
      <h1>Register new student to mobile money card</h1>
      <RAgentLogout disabled={isSubmitting} />
      {schoolsLoading && <h3>Loading schools from database. Please wait</h3>}
      {schoolsError && <h3>ERROR: {schoolsError}</h3>}
      {schools && !schoolsError && (
        <form onSubmit={handleSubmit(onSubmit)}>
          <div>
            <input
              type="text"
              placeholder="student name"
              {...register("name", { required: true })}
            />
            {errors.name && <span>student name is required</span>}
          </div>
          <div>
            <select
              {...register("schoolId", {
                required: true,
                validate: (value) => value !== "-- select school--",
              })}
            >
              <option hidden disabled selected>
                -- select school--
              </option>
              {schools.map((school) => (
                <option key={school.id} value={school.id}>
                  {school.name}
                </option>
              ))}
            </select>
            {errors.schoolId && <span>school is required</span>}
          </div>
          <div>
            <input
              type="text"
              placeholder="card number"
              {...register("cardNumber", { required: true })}
            />
            {errors.cardNumber && <span>card number is required</span>}
          </div>
          <button type="submit" disabled={isSubmitting}>
            {isSubmitting ? "CREATING STUDENT" : "CREATE STUDENT"}
          </button>
          {formMessage && <span>{formMessage}</span>}
        </form>
      )}
    </main>
  );
}

function Page() {
  return (
    <RAgentPrivateRoute>
      {(currentUser) => <RAgentRegisterStudent currentUser={currentUser} />}
    </RAgentPrivateRoute>
  );
}

export default Page;
