import { useState } from "react";
import { useForm } from "react-hook-form";
import { isValidPhoneNumber, parsePhoneNumber } from "libphonenumber-js";
import { confirmTransaction, initiateTransaction } from "../../services/send";
import { Link, useNavigate } from "react-router-dom";

let formMessageTimeout = null;

function Send() {
  const {
    register,
    handleSubmit,
    formState: { errors, isSubmitting },
  } = useForm();
  const navigate = useNavigate();

  const [formMessage, setFormMessage] = useState(null);
  const [transaction, setTransaction] = useState(null);
  const [completed, setCompleted] = useState(false);
  const [isConfirming, setIsConfirming] = useState(false);
  const [confirmError, setConfirmError] = useState(null);

  const onSubmit = async (data) => {
    setFormMessage(null);
    clearTimeout(formMessageTimeout);
    // console.log({
    //   ...data,
    //   senderNumber: parsePhoneNumber(data.senderNumber, "UG").format("E.164"),
    // });
    const payload = {
      ...data,
      senderNumber: parsePhoneNumber(data.senderNumber, "UG").format("E.164"),
    };
    try {
      const transaction = await initiateTransaction({ ...payload });
      console.log(transaction);
      setTransaction(transaction);
    } catch (error) {
      setFormMessage(`ERROR: ${error.message}`);
      formMessageTimeout = setTimeout(() => {
        setFormMessage(null);
      }, 4000);
    }
  };

  // const onConfirm = async () => {
  //   clearTimeout(formMessageTimeout);
  //   try {
  //     setIsConfirming(true);
  //     const { reference } = transaction;
  //     await confirmTransaction(reference);
  //     setCompleted(true);
  //   } catch (error) {
  //     setConfirmError(error.message);
  //     formMessageTimeout = setTimeout(() => {
  //       setConfirmError(null);
  //     }, 4000);
  //   } finally {
  //     setIsConfirming(false);
  //   }
  // };

  const beginTransaction = async () => {
    clearTimeout(formMessageTimeout);
    try {
      const { reference } = transaction;
      await confirmTransaction(reference);
    } catch (error) {
      setConfirmError(error.message);
      formMessageTimeout = setTimeout(() => {
        setConfirmError(null);
      }, 4000);
    }
  };

  const onConfirm = async () => {
    setIsConfirming(false);
    beginTransaction();
    setCompleted(true);
    setIsConfirming(false);
  };

  const renderForm = () => {
    return (
      <form onSubmit={handleSubmit(onSubmit)}>
        <div>
          <input
            type="text"
            placeholder="card number"
            {...register("cardNumber", {
              required: { value: true, message: "card number is required" },
              maxLength: {
                value: 16,
                message: "too many digits. 16 digits required",
              },
              minLength: {
                value: 16,
                message: "too few digits. 16 digits required",
              },
            })}
          />
          {errors.cardNumber && <span>{errors.cardNumber.message}</span>}
        </div>
        <div>
          <input
            type="tel"
            placeholder="mobile money number"
            {...register("senderNumber", {
              required: {
                value: true,
                message: "mobile money number is required",
              },
              validate: (value) =>
                !isValidPhoneNumber(value, "UG")
                  ? "invalid phone number"
                  : null,
            })}
          />
          {errors.senderNumber && <span>{errors.senderNumber.message}</span>}
        </div>
        <div>
          <input
            type="text"
            placeholder="amount to send"
            {...register("amount", {
              required: { value: true, message: "amount is required" },
              validate: (value) =>
                isNaN(value) ? "amount should be a number" : null,
              min: {
                value: 500,
                message: "500 is the minimum amount your can send",
              },
              valueAsNumber: true,
            })}
          />
          {errors.amount && <span>{errors.amount.message}</span>}
        </div>
        <div>
          <textarea
            cols="30"
            rows="10"
            placeholder="reason"
            {...register("reason", {
              maxLength: { value: 255, message: "255 max characters" },
              required: { value: true, message: "reason is required" },
              validate: (value) =>
                value.length === 0 ? "reason is required" : null,
            })}
          ></textarea>
          {errors.reason && <span>{errors.reason.message}</span>}
        </div>
        <button type="submit" disabled={isSubmitting}>
          {isSubmitting ? "PLEASE WAIT..." : "SEND MONEY"}
        </button>
        {formMessage && <span>{formMessage}</span>}
      </form>
    );
  };

  const renderConfirm = () => {
    const { amount, receiverName, serviceFee, senderNumber } = transaction;
    const LFee = serviceFee * 2 - serviceFee / 2;
    const HFee = serviceFee * 2 + serviceFee / 2;
    const LTotal = amount + LFee;
    const HTotal = amount + HFee;
    return (
      <div>
        <h3>Transaction details</h3>
        <div>Recipient Name: {receiverName}</div>
        <div>
          Amount to send: UGX {Intl.NumberFormat("en-US").format(amount)}
        </div>
        <div>Mobile money number: {senderNumber}</div>
        <div>
          Service Fee: {Intl.NumberFormat("en-US").format(LFee)} -{" "}
          {Intl.NumberFormat("en-US").format(HFee)}
        </div>
        <div>
          Total: {Intl.NumberFormat("en-US").format(LTotal)} -{" "}
          {Intl.NumberFormat("en-US").format(HTotal)}
        </div>
        {!completed && (
          <div>
            <button onClick={onConfirm} disabled={isConfirming}>
              {isConfirming ? "CONFIRMING" : "CONFIRM"}
            </button>
            <button
              onClick={() => {
                navigate("/send", { replace: true });
              }}
            >
              CANCEL
            </button>
          </div>
        )}
        {completed && renderCompleted()}
      </div>
    );
  };

  const renderCompleted = () => {
    const { senderNumber } = transaction;
    return (
      <div>
        Payment initiated. A payment prompt should appear on your mobile device.{" "}
        <br />
        If no prompt appears in one minute, refresh the page and try again.{" "}
        <br /> <br />
        Click
        <Link to={`/send/history/${senderNumber}`}> here</Link> to see your
        transaction history.
        {confirmError && <div>ERROR: {confirmError}</div>}
      </div>
    );
  };

  return (
    <main>
      <h1>Send money to MM Card account</h1>
      {!transaction && renderForm()}
      {transaction && renderConfirm()}
    </main>
  );
}

export default Send;
