import { useState } from "react";
import { useForm } from "react-hook-form";
import { Navigate } from "react-router-dom";
import { loginRAgent } from "../../services/r-agent";


let loginErrorTimeout = null;

function RAgentLogin() {
  const {
    register,
    handleSubmit,
    formState: { errors, isSubmitting },
  } = useForm();

  const [loginError, setLoginError] = useState(null);
  const [isLoginSuccess, setIsLoginSuccess] = useState(false);

  const onSubmit = async (data) => {
    try {
      await loginRAgent(data);
      setIsLoginSuccess(true);
    } catch (error) {
      setLoginError(error.message);
      clearTimeout(loginErrorTimeout);
      loginErrorTimeout = setTimeout(()=>{
        setLoginError(null);
      }, 4000);
    }
  };

  return (
    <main>
      {isLoginSuccess && <Navigate to="/r-agent" />}
      <form onSubmit={handleSubmit(onSubmit)}>
        <div>
          <input
            type="email"
            placeholder="email"
            {...register("email", { required: true })}
          />
          {errors.email && <span>email is required</span>}
        </div>
        <div>
          <input
            type="password"
            placeholder="password"
            {...register("password", { required: true })}
          />
          {errors.password && <span>password is required</span>}
        </div>
        <button type="submit" disabled={isSubmitting}>
          {isSubmitting ? "LOGGING IN" : "LOG IN"}
        </button>
        {loginError && <div>LOGIN ERROR: {loginError}</div>}
      </form>
    </main>
  );
}

export default RAgentLogin;
