import axios from "axios";

const api = axios.create();

export async function loginRAgent(logInData) {
  try {
    const response = await api.post("/api/r-agent/login", { ...logInData });
    const user = response.data;
    localStorage.setItem("user", JSON.stringify(user));
    return user;
  } catch (error) {
    if (error.response) {
      throw new Error(error.response.data.message);
    }
    // else if(error.request) {

    // }
    else {
      throw new Error("something went wrong");
    }
  }
}

export function logOutCurrentUser() {
  localStorage.removeItem("user");
}

export function getCurrentUser() {
  return JSON.parse(localStorage.getItem("user"));
}

export async function getSchools() {
  // const cachedSchools = localStorage.getItem("school-cache");
  // if (cachedSchools) {
  //   return JSON.parse(cachedSchools);
  // }
  try {
    const response = await api.get("/api/r-agent/school-list");
    const schools = response.data;
    localStorage.setItem("school-cache", JSON.stringify(schools));
    return schools;
  } catch (error) {
    if (error.response) {
      throw new Error(error.response.data.message);
    } else {
      throw new Error("something went wrong");
    }
  }
}

export async function createStudentAccount(studentData) {
  try {
    const response = await api.post("/api/r-agent/register-student", studentData);
    return await response.data;
  } catch (error) {
    if (error.response) {
      throw new Error(error.response.data.message);
    } else {
      throw new Error("something went wrong");
    }
  }
}
